













































































































import { Component, Vue } from 'vue-property-decorator';
import countryList from 'country-list';

import { getInternships } from '../../../api/internships';
import {
  IInternshipEntity,
  InternshipOpts,
  INTERNSHIP_MODE,
} from '../../../declarations';

import Pagination from '../../../components/Pagination/index.vue';

import { CategoriesModule } from '../../../store/modules/categories';

@Component({
  name: 'InternshipsMentorList',
  components: {
    Pagination,
  },
})
export default class extends Vue {
  private tableKey = 0;
  private list: IInternshipEntity[] = [];

  private total = 0;
  private listLoading = true;
  private listQuery: InternshipOpts = {
    page: 1,
    limit: 10,
    subject: undefined,
    countries: [],
    types: [],
    mode: [INTERNSHIP_MODE.AVAILABLE_CAMPAIGN],
    isAbroad: false,
  };

  private countryList = countryList.getNames();

  private get types() {
    return CategoriesModule.categories;
  }

  public created() {
    this.getList();
  }

  private getList() {
    this.listLoading = true;
    getInternships(this.listQuery).then((res: any) => {
      this.list = res ? res.data : [];
      this.total = res ? res.max : 0;
      this.listLoading = false;
    });
  }

  private handleFilter() {
    this.getList();
  }
}
